.popup {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, .5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
  opacity: 0;
  height: 0;
  overflow: hidden;
  transition-property: opacity, height;
  transition-duration: .4s, 0s;
  transition-delay: 0s, .4s;
}
